@media only screen and (max-width: 1040px) {

    .aboutMe {
        display: none;
    }

    .carousel {
        /* border: 1px solid black;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
        display: inherit;
        width: auto;
        height: 600px;
        overflow: hidden;
        overflow-y: scroll;
        margin: auto;
        margin-top: 30px;
        padding: 50px;
    }
    .carousel::-webkit-scrollbar{
    
        display: none;
    }

    .innerCarousel {
        /* border: 1px solid black; */
        display: flex;
        flex-direction: column;
        gap: 40px;
        width: 410px;
        margin: auto;
        /* cursor: grab; */
    }

    .imgDiv {
        min-width: 400px;
    }

    .box .socialContainer {
        flex-direction: column;
        /* border: 1px solid blue; */
    }

    .box .socialLinksBlackBox {

        width: 60vw;
        background-color: #fc6703;
    }

    .box .socialLinkCards {
        display: inherit;
    }

    #socialLinkBox {
        display: none;
    }
}


