.more:hover{
    cursor:pointer;
}
.projectCards .details{
    background-color: #1A1A1D;
    color: #b7b6b7;
    border-radius: 5px;
    padding: 5px;
    height: 150px;
}
.projectCards .details::-webkit-scrollbar{
    display: none;
}


@media only screen and (max-width: 1200px) {

    .mainContainer {
        width: 100vw;
        height: auto;
        margin-top: 50px;
    }

    #container {
        width: 100vw;
        /* display: initial; */
    }

    #container .projectCards {
        /* border: 1px solid black; */
        width: 40vw;
        height: inherit;
        margin: auto;
        margin-top: 0px;
        margin-bottom: 0px;

    }
    #firstTwo {
        width: 90vw;
        /* margin-top: 20px; */
        gap: 30px;
    }

    #lastTwo {
        width: 90vw;
        margin-top: 20px;
        gap: 30px;
    }
    .projectCards .details{
        height: 150px;
    }

}

