.MainBox {
    /* border: 1px solid black; */
    width: 100%;
    height: auto;

}

@media only screen and (max-width: 900px) {
    .MainBox {
        width: 100vw;
        height: auto;
        margin:auto;
        /* border: 1px solid black */
    }

    #SkillsMainContainer {
        /* display: flex; */
        gap: 20px;
        width: 99vw;
        height: auto;
        margin:auto;
        /* border: 5px solid orange */
    }

    .cards {
        width: 30%;
        height: auto;
        margin:auto;
        /* border: 5px solid rgb(252, 199, 100); */
    }

    .cards>#skillsBox {
        /* border: 1px solid black; */
        /* display: grid; */
        width: 80vw;
        margin: auto;
        font-size: 15px;
        grid-template-columns: repeat(4, 1fr);
    }

    .cards>#toolsBox {
        /* border: 1px solid black; */
        /* display: grid; */
        width: 80vw;
        margin: auto;
        font-size: 15px;
        grid-template-columns: repeat(4, 1fr);
    }
    
    .cards > #certificates{
        flex-direction: column;
        align-items: center;
        gap: 20px;
        margin: auto;
        width: 80%;
        /* border: 1px solid black; */
    }
}

@media only screen and (max-width: 640px) {

    .MainBox {
        width: 100vw;
        height: auto;
        /* border: 5px solid black; */
    }

    #SkillsMainContainer {
        width: 100vw;
        height: auto;
        display: initial;
        /* border: 5px solid orange */

    }

    .cards {
        /* margin: auto; */
        margin-top: 30px;
        height: auto;
        width: 70%;
    }

    .cards>#skillsBox {
        
        margin: auto;
        width: 70vw;
        font-size: 15px;
        grid-template-columns: repeat(3, 1fr);
    }

    .cards>#toolsBox {
        /* border: 1px solid black; */
        /* display: grid; */
        width: 70vw;
        margin: auto;
        font-size: 15px;
        grid-template-columns: repeat(3, 1fr);
    }
}

@media only screen and (max-width: 550px) { 

    .cards > #certificates{
        flex-direction: column;
        align-items: center;
        gap: 20px;
        width: 100%;
        /* margin: auto; */
        /* border: 1px solid black; */
    }

}

@media only screen and (max-width: 470px) {
    .MainBox {
        width: 100vw;
        margin:auto;
        /* border: 5px solid black; */
    }

    #SkillsMainContainer {
        width: 100vw;
        height: auto;
        display: initial;
        margin:auto;
        /* border: 5px solid orange */

    }

    .cards {
        margin: auto;
        margin-top: 30px;
        height: auto;
        width: 80%;
    }

    .cards>#skillsBox {
        /* border: 1px solid black; */
        width: 80vw;
        margin: auto;
        font-size: 15px;
        grid-template-columns: repeat(2, 1fr);
    }

    .cards>#toolsBox {
        /* border: 1px solid black; */
        width: 80vw;
        margin: auto;
        font-size: 15px;
        grid-template-columns: repeat(2, 1fr);
    }

    #heading1, #heading2,#heading3{
    
        width: 250px;
        font-size: 30px;
       
    }
    .cards > #certificates{
        flex-direction: column;
        gap: 20px;
        margin: auto;
        width: 100%;
        /* border: 1px solid black; */
    }

}