#image {
    display: block;
    /* margin-top: 20px; */
    width: 350px;
    /* color:  */
    border-radius: 10px;
    box-shadow: 0 0 0 2px rgb(238, 236, 236),
        2.3em 1.3em 1em rgba(0, 0, 0, 0.3);
}

#box #greeting {
    height: auto;
    width: auto;
}

#heading {
    /* text-shadow: 5px 5px #c3c0bf; */
    font-size: 45px;
}

#text1 {
    text-shadow: 5px 5px #c3c0bf;
    font-size: 30px;
}

#text2 {
    text-shadow: 5px 5px #c3c0bf;
    font-size: 30px;
}

#bgBox {
    border-radius: 10px;
    z-index: 1;
    position: absolute;
    width: 590px;
    height: 320px;
    margin-top: 250px;
    right: 65px;
}

.fs {
    opacity: 0;
    transition: opacity 5s, filter 5s;
    background: white;
    filter: blur(20px) contrast(10);
}

.g {
    background: white;
    opacity: 1;
    filter: blur(20px) contrast(10);
    transition: opacity 5s, filter 5s;
}



