.contactMainBox .social500 {
   display: none;
}
.contactMainBox .socialLinkText{
   display: none;
}

@media only screen and (max-width: 1000px) {

   .contactMainBox {
       height: auto;
       width: 100vw;
       /* border: 1px solid white; */
   }
}

@media only screen and (max-width: 800px) {

   .contactMainBox {
       height: auto

   }

   .contactMainBox .displayBox {
       display: none;
   }

   .contactMainBox .socialLinksGrayBox {
       display: flex;
       justify-content: space-around;

   }

   .contactMainBox .social500 {
       display: inherit;
   }




}

@media only screen and (max-width: 500px) {

   .contactMainBox {
       width: 100vw;
       height: auto;
       /* border: 1px solid white; */
   }

   .contactMainBox #subBox {
       /* border: 5px solid white; */
       width: auto;
       height: auto;
       display: initial;
       margin: auto;
       margin-top: 20px;
   }
   .contactMainBox .socialLinksGrayBox{
       flex-direction: column;
       gap: 30px;
       align-items: center;
   }
   .contactMainBox .social500{
       width: 70vw;
   }
   .contactMainBox .socialLinkText{
       display: inherit;
   }
   .contactMainBox .contactEmail{
       font-size: 15px;
   }
}