#NavMainBox{
    /* font-family: 'Alkatra', cursive; */
    font-family: 'Gruppo';
 
}

#menu{
      
    display: none;
}


@media only screen and (max-width: 1070px) {
    
    #NavMainBox{
        width: 100vw;
        height: auto;
    }
    #hamburger{
      
        display: none;
    }
    #menu{
      
        display: initial;
    }
    
}
@media only screen and (max-width: 750px) {
    
    #NavMainBox{
        width: 100vw;
        height: auto;
    }
    #hamburger{
      
        display: none;
    }
    #menu{
      
        display: initial;
    }
    
}

@media only screen and (max-width: 550px) {
    #heading{
        font-size: x-large;
    }
    #NavMainBox{
        width: 100vw;
        height: auto;
    }
    #container{
        height: auto;
    }
    #hamburger{
      
        display: none;
    }
    #menu{
      
        display: initial;
    }
    
}
